.infostrykt-list {
  position: absolute;
  background-color: FCFCFB;
  width: 331px;
  border-radius: 8px;
  opacity: 0.8;
  padding: 24px 32px;
}

.infostrykt-iten {
  display: flex;
  align-items: center;
}
.infostrykt-iten:not(:last-child) {
  margin-bottom: 60px;
}
.infostrykt-iten:nth-child(even) {
  flex-direction: row-reverse;
}
.infostrykt-text {
  border-radius: 50px;
  border: 1px solid var(--button-text, #2d2a22);
}
.infostrykt-time {
  color: var(--button-text, #2d2a22);
  text-align: center;

  /* on the map */
  font-family: Poppins;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  width: 72px;
}
