@media  (min-width: 1101px), (max-width: 1199px){
  .infostrykt-conteiner {
    width: 1100px;
  }
  .infostrykt-list {
    right: 800px;
  }
  .infostrykt{
    width: 100%;
  }
}

@media (max-width: 700px) {
  .infostrykt {
    width: auto;
    max-width: 700px;
  }
  .infostrykt-conteiner {
    .infostrykt {
      width: auto;
      max-width: 700px;
      // background: green;

      // back

      // infrastructure-backround-container-img-mobile.png
      background: (
          url('../img/infrastructure-backround-container-img-mobile.png')
        )
        no-repeat;
      // background: rgb(37, 251, 9);
    }
  }
}

@media (max-width: 700px), (min-width: 501px) {
  .infostrykt {
    width: auto;
    // max-width: 700px;
    max-width: 80%;
  }
  .infostrykt-conteiner {
    width: auto;
    max-width: 700px;
    width: 90%;
