    // back

    // infrastructure-backround-container-img-mobile.png
    background: url('../img/infrastructure-backround-container-img-mobile.png')
      no-repeat;
    background-size: 99% 99%;

    // background: rgb(37, 251, 9);

    // padding
    margin: 50px 30px;
  }

  .infostrykt-list {
    margin: 0 auto;
    right: 125px;
  }
}

