.menu-btn {
  width: 50px;
  z-index: 1;
  z-index: 1000;
  align-items: center;
  margin-top: 50px;
  margin-right: 10px;
  padding-bottom: 50px;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
}

.menu-btn > span, .menu-btn > span:before, .menu-btn > span:after {
  width: 100%;
  height: 5px;
  background-color: #fff;
  padding-top: 5px;
  font-size: xx-large;
  line-height: 10px;
  transition-duration: .25s;
  display: block;
  position: absolute;
}

.menu-btn > span:before {
  content: "";
  top: -15px;
}

.menu-btn > span:after {
  content: "";
  top: 15px;
}

.menubox {
  visibility: hidden;
  width: 100%;
  height: 100%;
  z-index: 99;
  text-align: center;
  background: linear-gradient(337deg, #2d2a22 5.7%, #7e6d53 108.98%);
  margin: 0;
  padding: 0;
  list-style: none;
  transition-duration: .25s;
  display: block;
  position: fixed;
  top: 0;
  left: -100%;
  box-shadow: 1px 0 6px rgba(0, 0, 0, .2);
}

.menu-item {
  color: #fff;
  padding: 5px;
  font-family: Inter;
  font-size: 20px;
  font-weight: 0;
  text-decoration: none;
  transition-duration: .25s;
  display: block;
}

.menu-item:hover {
  background-color: #ffa600;
}

#menu-toggle {
  opacity: 0;
}

#menu-toggle:checked ~ .menu-btn > span {
  transform: rotate(45deg);
}

#menu-toggle:checked ~ .menu-btn > span:before {
  transform: rotate(0);
  top: 0;
}

#menu-toggle:checked ~ .menu-btn > span:after {
  top: 0;
  transform: rotate(90deg);
}

#menu-toggle:checked ~ .menubox {
  visibility: visible;
  left: 0;
}

